import stepOne from './step1.vue';
import stepTwo from './step2.vue';
import stepThree from './step3.vue';
import stepFour from './step4.vue';
export default {
	data() {
		return {
			numList: [
				{
					name: '任务信息'
				},
				{
					name: '招募信息'
				},
				{
					name: '人员招募筛选'
				},
				{
					name: '开始生产'
				}
			],
			form_title:['任务基本信息','招募信息','任务需求'],
			num: 0,
			scrollTop: 0,
			old: {
				scrollTop: 0
			},
			task_id:'',
		};
	},
	components: {
		stepOne,
		stepTwo,
		stepThree,
		stepFour
	},
	onLoad(option) {
		console.log(option.id)
	},
	methods: {
		next(){
			switch (this.num){
				case 0:
					this.$refs.stepOne.formSubmit((id) =>{
						
						this.num++
						this.task_id = id
					})
					break;
				case 1:
					this.$refs.stepTwo.formSubmit(() =>{
						
						this.num++
					})
					break;
				default:
					break;
			}
			// this.num++
			this.scrollTop = this.old.scrollTop
			this.$nextTick(function() {
				this.scrollTop = 0
			});
			if(this.num ==2){
				this.$store.commit('setHasTask',true)
			}
		},
		prev(){
			this.num--
		},
		scroll(e){
			// console.log(e)
			this.old.scrollTop = e.detail.scrollTop
		},
	}
};
